import { _decorator, Component, Label, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('DetailsView')
export class DetailsView extends Component {
    @property(Label)
    catName: Label = null;
    @property(Label)
    catMs: Label = null
    @property(Label)
    lvLoad:Label = null;
    @property(Label)
    lvLab: Label = null;
    @property(Label)
    dayLab: Label = null;
    @property(Label)
    birthday: Label = null;
    @property(Label)
    weight: Label = null;
    @property(Label)
    pinKLab: Label = null;

    protected onLoad(): void {
    
    }

    public initConfig(data: any): void{
        this.catName.string = data.name;
        this.catMs.string = data.ms;
    }


    public ClickFeeding(){
        
    }

}


